.specimenRoot {
  margin-top: 1.5rem;
}

.specimenContainer {
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  overflow-x: auto;
  -webkit-overflow-scroll: smooth;
}

.specimen {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  display: inline-block;
  white-space: nowrap;
}

.fontLargeV { font-size: 2.75rem; }
.fontLargeIv { font-size: 2rem; }
.fontLargeIii { font-size: 1.75rem; }
.fontLargeIi { font-size: 1.5rem; }
.fontLargeI { font-size: 1.1875rem; }
.fontRegular { font-size: 1rem; }
.fontSmallI { font-size: 0.875rem; }
.fontSmallIi { font-size: 0.6875rem; }

.bold { font-weight: 800; }
.demi { font-weight: 700; }
.regular { font-weight: 400; }

.weightGroup {
  margin-top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}

.weight {
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  max-width: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  width: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  margin-top: 1.5rem;
}

.weight:nth-child(1n) {
  margin-right: 1rem;
  margin-left: 0;
}

.weight:last-child {
  margin-right: 0;
}

.weight:nth-child(1n) {
  margin-right: 0;
  margin-left: auto;
}

@media(min-width: 25rem) {
  .weight {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
    max-width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
    width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
  }
  .weight:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .weight:last-child {
    margin-right: 0;
  }
  .weight:nth-child(2n) {
    margin-right: 0;
    margin-left: auto;
  }
}

@media(min-width: 50rem) {
  .weight {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    max-width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
  }
  .weight:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .weight:last-child {
    margin-right: 0;
  }
  .weight:nth-child(3n) {
    margin-right: 0;
    margin-left: auto;
  }
}




