:global(html),
:global(body),
:global(#root) {
  height: 100%;
}

.root {
  position: relative;
}

.navigationSm {
  padding-top: 3rem;
  padding-bottom: 1.5rem;
}

.navigationLg {
  display: none;
}

.body {
  padding-top: calc(3 * 1rem);
  padding-bottom: 3rem;
  width: 100%;
}

.menuBtn {
  position: absolute;
  top: 0;
  left: 0;
  padding: 1rem;
  color: rgb(56, 56, 56);
}

@media(min-width: 56.25rem) {
  .root {
    display: -ms-flexbox;
    display: flex;
    min-height: 100%;
  }

  .menuBtn {
    display: none;
  }

  .navigationSm {
    display: none;
  }

  .navigationLg {
    display: block;
    padding-top: 3rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    width: 30%;
    max-width: 25rem;
    position: relative;
    border-right: 1px solid rgb(221, 221, 221);
    position: fixed;
    overflow-y: scroll;
    height: 100%;
  }

  .body {
    margin-top: 0;
    padding: 3rem;
    min-height: 100%;
    width: 70%;
    right: 0;
    position: absolute;
  }

  .wrapper {
    max-width: 50rem;
  }
}



