.root {
  position: relative;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(221, 221, 221);
  border-radius: 4px;
}

.specimenContainer {
  position: relative;
  z-index: 1;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.light {
  background-color: rgb(255, 255, 255);
  color: rgb(25, 25, 25);
}

.dark {
  background-color: rgb(56, 56, 56);
  color: rgb(255, 255, 255);
}

.body {
  border-top: 1px solid rgb(221, 221, 221);
  text-align: left;
  padding: 1rem;
}

.name {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 1rem;
  line-height: 1.4375;
  letter-spacing: 0.010625rem;
  font-weight: 700;
  color: rgb(25, 25, 25);
}

.attributes {
  margin: 0;
  margin-top: 0.25rem;
  list-style-type: none;
}

.attribute {
  font-family: 'Fira Mono', monospace;
  color: rgb(139, 139, 139);
}

.attribute + .attribute {
  margin-top: 0.25rem;
}

.codeBlock {
  overflow-x: auto;
  -webkit-overflow-scroll: smooth;
}

.name + .codeBlock,
.attributes + .codeBlock {
  margin-top: 0.25rem;
}

.code {
  font-family: 'Fira Mono', monospace;
}



