.brand,
.icon {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  position: relative;
}

.icon svg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.regular {
  font-size: 1.5rem;
}

.large svg {
  font-size: 4rem;
}



