.imageContainer {
  height: 9rem;
  width: 16rem;
  min-height: 4.5rem;
  min-width: 8rem;
  resize: both;
  overflow: scroll;
}

.image {
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgb(245, 245, 245);
}

.cover {
  -o-object-fit: cover;
     object-fit: cover;
}

.contain {
  -o-object-fit: contain;
     object-fit: contain;
}



