.exampleModal {
  width: 100vw;
  height: 100vh;
  max-width: 33.333333333rem;
  max-height: 20rem;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(221, 221, 221);
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 1rem;
  border-radius: 2px;
}



