.iconGroup {
  margin-top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.icon {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
  max-width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
  width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
  margin-top: 1.5rem;
}

.icon:nth-child(1n) {
  margin-right: 1rem;
  margin-left: 0;
}

.icon:last-child {
  margin-right: 0;
}

.icon:nth-child(2n) {
  margin-right: 0;
  margin-left: auto;
}

@media(min-width: 31.875rem) {
  .icon {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    max-width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
  }
  .icon:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .icon:last-child {
    margin-right: 0;
  }
  .icon:nth-child(3n) {
    margin-right: 0;
    margin-left: auto;
  }
}

@media(min-width: 50rem) {
  .icon {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/4 - (1rem - 1rem * 1/4));
    max-width: calc(99.9% * 1/4 - (1rem - 1rem * 1/4));
    width: calc(99.9% * 1/4 - (1rem - 1rem * 1/4));
  }
  .icon:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .icon:last-child {
    margin-right: 0;
  }
  .icon:nth-child(4n) {
    margin-right: 0;
    margin-left: auto;
  }
}



