.markdown * {
  margin-top: 0;
  margin-bottom: 0;
}

.markdown * + * {
  margin-top: 1.5rem;
}

.markdown * + h2,
* + .h2 {
  margin-top: 4rem;
}

.markdown h1 {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 2rem;
  line-height: 1.34375;
  letter-spacing: 0.01875rem;
  font-weight: 700;
  color: currentColor;
}

.markdown h2 {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 1.75rem;
  line-height: 1.28571;
  letter-spacing: 0.01875rem;
  font-weight: 700;
  color: currentColor;
}

.markdown h3 {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 1.5rem;
  line-height: 1.333;
  letter-spacing: 0.01875rem;
  font-weight: 700;
  color: currentColor;
}

.markdown ul,
.markdown ol {
  list-style-position: outside;
  padding-left: 0;
}

.markdown p,
.markdown li {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 1rem;
  line-height: 1.4375;
  letter-spacing: 0.010625rem;
  font-weight: 400;
  color: currentColor;
}

.markdown li + li {
  margin-top: 0.25rem;
}

.markdown p:first-of-type {
  font-family: 'Avenir Next W01', 'Helvetica Neue', 'Helvetica', 'sans-serif';
  font-size: 1.1875rem;
  line-height: 1.263;
  letter-spacing: 0.0125rem;
  font-weight: 700;
  color: currentColor;
}

.markdown a {
  color: rgb(186, 160, 130);
  text-decoration: underline;
}

.markdown a:hover {
  color: rgb(219, 189, 154);
}



