.root {
  position: relative;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(221, 221, 221);
  border-radius: 4px;
  background-color: rgb(255, 255, 255);
  color: rgb(25, 25, 25);
  padding: 1rem;
  overflow-x: auto;
  -webkit-overflow-scroll: smooth;
}

.code {
  font-family: 'Fira Mono', monospace;
}



