.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: 70;
  background-color: rgba(20, 20, 21, 0.5);
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 70;
  overflow-y: scroll;
  width: 100%;
  max-width: 20rem;
  background-color: rgb(255, 255, 255);
}

.closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  color: rgb(56, 56, 56);
}

.closeIcon {
  line-height: 0;
}



