.root {
  padding-bottom: 1rem;
}

.section + .section {
  margin-top: 3rem;
}

.title {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.linkList {
  list-style-type: none;
  margin-top: 0.75rem;
  margin-left: 0;
  margin-left: 0;
  margin-bottom: 0;
  padding: 0;
}

.link {
  display: block;
  color: rgb(139, 139, 139);
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 3rem;
  padding-right: 3rem;
  text-decoration: none;
}

.link:hover {
  color: rgb(35, 35, 35);
}

.linkActive {
  font-weight: 700;
  color: rgb(35, 35, 35);
}

.linkListItem + .linkListItem .link {
  margin-top: 0.5rem;
}



