.colorGroup {
  margin-top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}

.color {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  max-width: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  width: calc(99.9% * 1/1 - (1rem - 1rem * 1/1));
  margin-top: 1.5rem;
}

.color:nth-child(1n) {
  margin-right: 1rem;
  margin-left: 0;
}

.color:last-child {
  margin-right: 0;
}

.color:nth-child(1n) {
  margin-right: 0;
  margin-left: auto;
}

@media(min-width: 25rem) {
  .color {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
    max-width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
    width: calc(99.9% * 1/2 - (1rem - 1rem * 1/2));
  }
  .color:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .color:last-child {
    margin-right: 0;
  }
  .color:nth-child(2n) {
    margin-right: 0;
    margin-left: auto;
  }
}

@media(min-width: 50rem) {
  .color {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    max-width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
    width: calc(99.9% * 1/3 - (1rem - 1rem * 1/3));
  }
  .color:nth-child(1n) {
    margin-right: 1rem;
    margin-left: 0;
  }
  .color:last-child {
    margin-right: 0;
  }
  .color:nth-child(3n) {
    margin-right: 0;
    margin-left: auto;
  }
}



